import React from "react";

export default function ProjectButtons(props) {
  return (
    <div className="project-button-container">
      {
        props.liveSite
          ? (
            <a
              href={props.liveSite}
              className="btn-live-site"
              style={{ backgroundColor: props.color }}
              target="_blank"
            >
              Live site
            </a>
          ) : null
      }
      {
        props.repo
          ? (

            <a
              href={props.repo}
              className="btn-git-repo"
              target="_blank"
            >
              Git repo
            </a>
          ) : null
      }
    </div>
  );
}



