import React from "react";
import { Link } from "react-router-dom";

import AnimatedPage from "../components/AnimatedPage";

class Projects extends React.Component {
  constructor(props) {
    super(props);

    this.projects = [
      { to: "uppskriftir-allra", label: "UppskriftirAllra.is" },
      { to: "spotify-ddr", label: "Spotify DDR" },
      { to: "bezier", label: "Vector Networks" },
      { to: "riff", label: "Reykjavík International Film Festival" },
    ];
  }

  componentDidMount() {
    this.projects.forEach(({ to }, i) => {
      const canvas = document.getElementById(`project-${to}`);
      canvas.width = 0;
      canvas.height = 0;
      canvas.width = canvas.parentNode.offsetWidth;
      canvas.height = canvas.parentNode.offsetHeight;
      const ctx = canvas.getContext("2d");
      this.projects[i] = {
        ...this.projects[i],
        width: canvas.width,
        height: canvas.height,
        ctx,
      };
    });

    const amplitude = 30;

    this.animate = () => {
      if (window.location.pathname === "/projects") {
        const now = Date.now();
        
        this.projects.forEach(({ to, ctx, width, height }, j) => {
          ctx.clearRect(0, 0, width, height);
          ctx.fillStyle = "white";
          for (let i = 0; i < 5; i += 1) {
            const w = (Math.sin((now + ((j * 5 + i) * 500)) / 1000) + 1) / 2 * amplitude;
            ctx.beginPath();
            ctx.rect(width - w, height * i / 5, w, height / 5);
            ctx.fill();
            ctx.closePath();
          }
        });
      }
      requestAnimationFrame(this.animate);
    };
    this.animate();

    window.addEventListener("resize", () => {
      this.projects.forEach((project) => {
        const canvas = document.getElementById(`project-${project.to}`);
        canvas.width = 0;
        canvas.height = 0;
        canvas.width = canvas.parentNode.offsetWidth;
        canvas.height = canvas.parentNode.offsetHeight;
        project.width = canvas.width;
        project.height = canvas.height;
      });
    });
  }

  render() {
    return (
      <div className="project-container">
        <div className="page-content-container">
          <div className="project-content">
            <h1 style={{ marginTop: "8rem", marginBottom: "3rem" }}>Projects</h1>
            {
              this.projects
                .map(({ to, label }) => (
                  <Link key={to} className="project-link" to={`/projects/${to}`}>
                    <div
                      className="projects-link-img"
                      style={{
                        backgroundImage: `url("/images/${to}-thumbnail.jpg")`,
                      }}
                    >
                      <canvas id={`project-${to}`} />
                    </div>
                    <div>
                      <h4>{label}</h4>
                    </div>
                  </Link>
                ))
            }
          </div>
        </div>
      </div>
    );
  }
}

export default AnimatedPage(Projects, "secondary-page-wrapper");



