import React from "react";
import Project from "../Project";
import TechUsed from "../../components/TechUsed";
import ProjectButtons from "../../components/ProjectButtons";

import constants from "../../constants";

class RIFF extends React.Component {
  constructor(props) {
    super(props);
    this.primaryColor = "#2B8BFC";
  }

  render() {
    return (
      <div className="project-container">
        <div className="page-content-container">
          <div className="project-content">
            <h1 style={{ color: this.primaryColor, marginTop: "8rem" }}>
              <span style={{ fontSize: ".9em" }}>
                Reykjavík Inter&shy;national Film Festival
              </span>
            </h1>
            <ProjectButtons
              color={this.primaryColor}
              liveSite="https://riff.xlex.is"
            />
            <TechUsed
              items={["react", "nextjs", "nodejs", "scss"]}
            />
            <p>
              Final project in school for our first semester and a proposal for RIFF's new website.
            </p>
            <p>
              The project was developed with the organizers of the festival to meet their needs, demands and hopes for the new website.
            </p>
            <p>
              The project groups assessed their old website from a functional standpoint, aesthetic standpoint and an accessibility standpoint and wrote a report. Then each team member would design and program a prototype which should address the points brought up in the report.
            </p>
            <figure>
              <img src="/images/riff-home.jpg" alt="" />
              <figcaption>
                Top of the front page during nav animation
              </figcaption>
            </figure>
            <p>
              Every site needs content, and theirs had plenty. Unfortunately we didn't have access to their database so I wrote a script that scraped their site and fetched every single film and parsed the data to create a custom film API.
            </p>
            <h3>Main features</h3>
            <ul>
              <li>Home page, film search page and a single film page.</li>
              <li>Renders pages from their WP database using React + Next.js.</li>
              <li>Icelandic and English language support.</li>
              <li>Custom film API.</li>
              <li>Basic film search.</li>
              <li>404 page.</li>
            </ul>
            <h3>Tech stack</h3>
      	    <ul>
              <li>Next.js with React for server side rendering.</li>
              <li>Built a scraper to fetch the film data.</li>
              <li>Served different sizes of the original images with sharp.</li>
            </ul>
          </div>
        </div>
      </div>
    );
  }
}

export default Project(RIFF, constants.projectColors.riff);



