import React from "react";

import AnimatedPage from "../components/AnimatedPage";
import TechUsed from "../components/TechUsed";

function About() {
  return (
    <div className="project-container">
      <div className="page-content-container about-container">
        <div className="project-content">
          <h1 style={{ marginTop: "8rem", marginBottom: "2rem" }}>About me</h1>
          <p>Hey, my name is Alex and I was born in 1997.</p>
          <p>
            I like programming, especially in Javascript. I also enjoy math, especially if I can use it in cool ways to visually enhance projects.
          </p>
          <p>
            I specialize in front end development and also dabble in the back end. Doesn't matter if it's programming a responsive layout, a single page app, a simple REST API or some slick loader animations.
          </p>
          <p>
            If it's Javascript I'll do it.
          </p>
          <h4>Tech I like</h4>
          <p>
            These are the main technologies I've used in past projects. It's not an exhaustive list but shows where my experience lies.
          </p>
          <TechUsed
            useTitle={false}
            items={[
              "react",
              "redux",
              "scss",
              "graphql",
              "apollo",
              "nodejs",
              "threejs",
              "mysql",
              "html",
              "css",
              "webpack",
              "babel",
            ]}
          />
          <h4>Education</h4>
          <div className="about-study-container">
            <h5>2017 - Now</h5>
            <p>
              Currently studying web development and design at <a className="link" href="https://vefskoli.is" target="_blank">Vefskólinn</a>.
            </p>
            <h5>2013 - 2017</h5>
            <p>
              Studied graphic design at <a className="link" href="http://bhs.is" target="_blank">Borgarholtsskóli</a>.
            </p>
          </div>
          <h4>Interests</h4>
          <p>
            Programming and design are the big two.
          </p>
          <p>
            I first opened Photoshop when I was 13 and played around in it and learned the basics. At 15 I got a bit more serious about it and decided to learn graphic design at school. Also branched out to other programs such as After Effects, Illustrator, and now Figma.
          </p>
          <p>
            At the beginning of 2017 I decided to learn web development. Then I ended up not really doing anything else. I started feeling genuinely passionate about it in a way that design never really did. So now I classify myself as a front end developer but still design on the side.
          </p>
          <p>
            I also like drawing, music and movies like everyone else.
          </p>
          <h4>Contact me</h4>
          <p>
            Got a project idea, job interview, or some other question? Hit me up.
          </p>
          <div className="contact-link-container">
            <i className="icon-small mail-icon" />
            <a className="link" href="mailto:alexharri2919@gmail.com">alexharri2919@gmail.com</a>
          </div>
          <div className="contact-link-container">
            <i className="icon-small phone-icon" />
            <a className="link" href="tel:+3546181997">618 1997</a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default AnimatedPage(About, "secondary-page-wrapper");



