import React from "react";

import Nav from "./Nav";

export default class Layout extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      navOpen: false,
    };

    this.setOpen = this.setOpen.bind(this);
  }

  setOpen(isOpen) {
    this.setState({
      navOpen: isOpen,
    });
  }

  render() {
    return (
      <div className="root">
        <Nav setOpen={this.setOpen} />
        <div
          className={"page-transition-container" + (this.state.navOpen ? " nav-open" : "")}
        >
          {
            this.props.children
          }
        </div>
      </div>
    );
  }
}



