import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from "react-router-dom";
import TransitionGroup from "react-transition-group/TransitionGroup";

import Layout from "./js/components/Layout";
import Home from "./js/pages/Home";
import About from "./js/pages/About";
import Projects from "./js/pages/Projects";
import Bezier from './js/pages/projects/Bezier';
import SpotifyDDR from './js/pages/projects/SpotifyDDR';
import UppskriftirAllra from './js/pages/projects/UppskriftirAllra';
import RIFF from './js/pages/projects/RIFF';

const firstChild = props => {
  const childrenArray = React.Children.toArray(props.children);
  return childrenArray[0] || null;
};

class App extends Component {
  render() {
    return (
      <div className="App">
        <Router>
          <Layout>
            {
              [
                { path: "/", component: Home, exact: true },
                { path: "/about", component: About },
                { path: "/projects", component: Projects, exact: true },
                { path: "/projects/bezier", component: Bezier },
                { path: "/projects/spotify-ddr", component: SpotifyDDR },
                { path: "/projects/uppskriftir-allra", component: UppskriftirAllra },
                { path: "/projects/riff", component: RIFF },
              ]
                .map(({ exact, path, component: Component }) => (
                  <Route
                    exact={!!exact}
                    key={path}
                    path={path}
                    children={({ match, ...rest }) => (
                      <TransitionGroup component={firstChild}>
                        {match && <Component {...rest} />}
                      </TransitionGroup>
                    )}
                  />
                ))
            }
            <canvas id="background-canvas" />
          </Layout>
        </Router>
      </div>
    );
  }
}

export default App;



